import { useEffect } from "react"

function ResumeSteps({completedSteps}){

    useEffect(() => {
        let steps_container = document.querySelectorAll(".step");
        steps_container.forEach((step, index) => {
            if(index < completedSteps){
                step.classList.add("step-completed")
            }
        })
    })

    return(
        <div className="resume-steps-wrapper">
            <div className="steps">
                <div id="one" className="step">
                    <i className="bi bi-check"></i>
                    <div className="step-title">Personal Information</div>
                </div>
                <div className="step">
                    <i className="bi bi-check"></i>
                    <div className="step-title">Professional Details</div>
                </div>
                <div className="step">
                    <i className="bi bi-check"></i>
                    <div className="step-title">Add Your Skills</div>
                </div>
                {/* <div className="step">
                    <i className="bi bi-check"></i>
                    <div className="step-title">Add Your Relevant Experience</div>
                </div> */}
            </div>
        </div>
    )
}

export {ResumeSteps}